











































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
export default {
	mixins:[mixin],
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			memberlist:[],
			is_show:false,
			code:''
		};
	},
	methods:{
		async init(){
			// 生成二维码
			Utils.loading('正在加载')
			let codeResult = await api.makeCode({bid:this.userInfo.bid})
			if(codeResult.status == 1){
				this.code = codeResult.data
			}
			this.initMember()
		},
		// 获取店员列表
		async initMember(){
			let memberResult = await api.getMemberList({
				bid:this.userInfo.bid
			})
			if(memberResult.status == 1){
				Utils.loaded()
				let memberList = []
				let memberData = memberResult.data
				memberData ? memberData.map((v,i) => {
					memberList.push({
						id:v.uid,
						member_title:v.nickname,
						member_ava:v.avatar,
						member_time:v.createtime
					})
				}):[]
				this.memberlist = memberList
			}else{
				Utils.toast(memberResult.message)
			}
		},
		deleteMember(id){
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认删除该店员？',
				success: function (res) {
					if (res.confirm) {
						_this.deleteAction(id)
					} else if (res.cancel) {
						
					}
				}
			});
		},
		async deleteAction(id){
			Utils.loading('正在删除..')
			let deleteResult = await api.deleteMember({
				uid:id
			})
			if(deleteResult.status == 1){
				Utils.loaded()
				this.initMember()
			}else{
				Utils.loaded()
				Utils.toast(deleteResult.message)
			}
		},
		showModel(){
			let isshow = this.is_show
			if(isshow){
				this.is_show = false
				uni.showTabBar({
					
				})
			}else{
				this.is_show = true
			}
		},
		
		
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	}
}
